<template>
  <div id="app">
    <audio
      v-if="song"
      :src="` https://music.163.com/song/media/outer/url?id=${song?.id}.mp3`"
      autoplay
      ref="audio"
      @pause="playMusic = false"
      @play="playMusic = true"
      @durationchange="duration = $event.target.duration"
      @timeupdate="updateTime = $event.target.currentTime"
      @ended="changeStatus('next')"
    ></audio>

    <MusicPlayBar
      :song="song"
      :playMusic="playMusic"
      @Update-play="$refs.audio.play()"
      @Update-stop="$refs.audio.pause()"
      @open-Page="
        openPage = true;
        songListShow = false;
      "
      :updateTime="updateTime"
      :duration="duration"
      @show-List="songListShow = !songListShow"
    />
    <MusicPlayPage
      @open-Page="
        openPage = false;
        songListShow = false;
      "
      @show-List="songListShow = !songListShow"
      v-show="song"
      :song="song"
      :playMusic="playMusic"
      :openPage="openPage"
      @Update-play="$refs.audio.play()"
      @Update-stop="$refs.audio.pause()"
      :updateTime="updateTime"
      :duration="duration"
      @upDate-time="$refs.audio.currentTime = $event"
      @toggle-Status="toggleStatus"
      :nowStatus="nowStatus"
      :loopStatus="loopStatus"
      @change-song="changeSong"
      :songList="songList"
    />

    <SongListPage
      @markclick-show="songListShow = false"
      :songList="songList"
      :songListShow="songListShow"
      :song="song"
      @Updatd-song="play"
      :nowStatus="nowStatus"
      @clear-List="songList = []"
      @clear-song="clearSong"
      @toggle-Status="toggleStatus"
    />
    <!--   60rem -->
    <!-- <keep-alive> -->

    <router-view
      @Updatd-song="play"
      @song-List="songList = $event"
      :style="`marginBottom:${song ? '60rem' : '0'}`"
      :nowSong="song"
    />
    <!-- </keep-alive> -->
  </div>
</template>
<script>
import MusicPlayBar from "@/components/musicPlayBar.vue";
import MusicPlayPage from "@/components/musicPlayPage.vue";
import SongListPage from "./components/songListPage.vue";
export default {
  components: {
    MusicPlayBar,
    MusicPlayPage,
    SongListPage,
  },
  data: function () {
    return {
      song: JSON.parse(localStorage.getItem("song")) || null,
      playMusic: false,
      openPage: false,
      updateTime: 0,
      duration: 0,
      songList: JSON.parse(localStorage.getItem("songList")) || [],
      songListShow: false,
      //    
      loopStatus: ["", "", ""],
      //  ['songAllLoop-bai-48','songoldLoop-bai-48','songRandom-bai-48'],
      nowStatus: localStorage.getItem("nowStatus") || "",
    };
  },
  methods: {
    play: function (res) {
      (this.playMusic = true),
        // console.log(res);
        (this.song = res);
    },
    songStatus: function () {
      this.playMusic = !this.playMusic;
    },

    toggleStatus: function (res) {
      let nowIndex = this.loopStatus.findIndex((o) => o == res);
      this.nowStatus = this.loopStatus[
        nowIndex == this.loopStatus.length - 1 ? 0 : nowIndex + 1
      ];
    },

    changeStatus: function (status) {
      switch (this.nowStatus) {
        case "":
          this.$refs.audio.loop = false;
          {
            var createdIndex = this.songList.findIndex((o) => o.id == this.song.id);
            if (status == "next") {
              // console.log("next");
              this.song = this.songList[
                createdIndex === this.songList.length - 1 ? 0 : createdIndex + 1
              ];
            } else {
              // console.log("prev");
              this.song = this.songList[
                createdIndex === 0 ? this.songList.length - 1 : createdIndex - 1
              ];
            }
          }
          break;
        case "":
          this.$refs.audio.currentTime = 0;
          this.$refs.audio.play();
          break;
        case "":
          this.$refs.audio.loop = false;
          var num = this.songList.filter((o) => o?.id !== this?.song?.id);
          var n = Math.floor(Math.random() * (num.length));
          this.song = num[n];
          break;
      }
    },
    changeSong: function (res) {
      if (this.nowStatus == "") {
        var createdIndex = this.songList.findIndex((o) => o.id == this.song.id);
        if (res == "next") {
          // console.log("next", res);
          this.song = this.songList[
            createdIndex === this.songList.length - 1 ? 0 : createdIndex + 1
          ];
        } else {
          // console.log("prev", res);
          this.song = this.songList[
            createdIndex === 0 ? this.songList.length - 1 : createdIndex - 1
          ];
        }
      } else {
        this.changeStatus(this.nowStatus);
      }
    },
    clearSong(res) {
      // console.log(
      //   res,
      //   this.songList.filter((o) => o.id == res)
      // );
      localStorage.removeItem("songList");
      this.songList = this.songList.filter((o) => o.id !== res);
    },
  },
  watch: {
    song(res) {
      localStorage.setItem("song", JSON.stringify(res));
    },
    nowStatus(res) {
      localStorage.setItem("nowStatus", res);
    },
    songList(res) {
      localStorage.setItem("songList", JSON.stringify(res));
    },
  },
};
</script>
<style lang="less">
* {
  font-size: 12rem;
}
@themeColor: #d43c33;
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
audio {
  position: fixed;
  bottom: 50rem;
  width: 100%;
  z-index: 1;
}
</style>
