<template>
  <div>
    <div
      class="mark"
      :class="!songListShow ? 'markNull' : ''"
      @click.stop="$emit('markclick-show')"
    ></div>
    <div class="songListBox" :class="songListShow ? 'boxShow' : ''">
      <div class="songListHeader">
        <div class="songListHeaderBox">
          <h3>
             <span>{{ `(${songList?.length})` }}</span>
          </h3>
          <div class="listModel">
            <div class="loop" @click="$emit('toggle-Status', nowStatus)">
              <span >{{ nowStatus }}</span>
            </div>
            <div class="clearList" @click="$emit('clear-List')"></div>
            <!-- @click="" -->
          </div>
        </div>
      </div>
      <ul class="List min" v-show="songList">
        <li
          class="songListItem"
          v-for="item in songList"
          :class="item?.id === song?.id ? 'active' : ''"
          :key="item.id"
          @click.stop="$emit('Updatd-song', item)"
        >
          <div class="information-left">
            <div class="play" v-show="item?.id === song?.id">
              <div class="playing" v-for="(key, index) in 4" :key="index"></div>
            </div>
            <span class="artist">{{ item?.name }}</span
            >&nbsp;-&nbsp;
            <span class="album">{{ artistAll(item?.song?.artists || item?.ar) }}</span>
          </div>
          <div class="right">
            <div v-if="false">
              <span></span>
            </div>
            <div class="clearItem" @click.stop="$emit('clear-song', item.id)">
            <svg t="1664434542745" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2651" width="48" height="48"><path d="M547.2 512l416-416c9.6-9.6 9.6-25.6 0-35.2s-25.6-9.6-35.2 0l-416 416-416-416c-9.6-9.6-25.6-9.6-35.2 0s-9.6 25.6 0 35.2l416 416-416 416c-9.6 9.6-9.6 25.6 0 35.2s25.6 9.6 35.2 0l416-416 416 416c9.6 9.6 25.6 9.6 35.2 0s9.6-25.6 0-35.2L547.2 512z" p-id="2652"></path></svg></div>
          </div>
        </li>
      </ul>
      <ul class="List" v-show="!songList.length">
        <div class="nullList">~</div>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    songList: Array,
    songListShow: Boolean,
    song: Object,
    nowStatus: String,
  },
  methods: {
    artistAll: function (arr) {
      let arrName = [];
      for (let i = 0; i < arr.length; i++) {
        arrName.push(arr[i].name);
      }
      return arrName.join("/");
    },
  },
};
</script>
<style lang="less" scoped>
.flex() {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.mark {
  position: fixed;
  transition: all 0.3s;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 300;
  background-color: rgba(0, 0, 0, 0.5);
  &.markNull {
    bottom: 100%;
    opacity: 0;
    visibility: hidden;
  }
}

.songListBox {
  width: 100%;
  position: fixed;
  transition: all 0.3s linear;
  bottom: -200vh;
  box-shadow: 0 0 20rem 0 #000;
  left: 0;
  z-index: 300;
  border-radius: 15rem;
  background-color: rgb(255, 255, 255);
  padding: 10rem 20rem;
  transform: scale(0.95);
  &.boxShow {
    bottom: 5rem;
  }
  .songListHeader {
    .songListHeaderBox {
      border-bottom: 1rem solid rgb(229, 229, 229);
      h3 {
        font-size: 15rem;
        margin: 10rem 0;
        span {
          font-size: 12rem;
        }
      }
      .listModel {
        margin: 10rem 0;
        .flex();
        .loop {
        }
        .clearList {
          padding: 5rem 10rem;
          border-radius: 50rem;
          box-shadow: 0 0 1rem 0 inset rgba(0, 0, 0, 0.545);
        }
      }
    }
  }
  .List {
    overflow-y: auto;
    &.min{
      min-height: 300rem;
    }
    .nullList {
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 300rem;
    }
    max-height: 300rem;
    .songListItem {
      padding-top: 10rem;
      margin-bottom: 5rem;
      .flex();
      &.active {
        span {
          color: red;
        }
      }

      .information-left {
        .flex();
        .play {
          .flex();
          justify-content: space-around;
          width: 30rem;
          height: 30rem;
          padding: 3.5rem 3.5rem 3.5rem 0;
          @keyframes informationMove {
            0% {
              transform: rotateX(60deg);
            }
            50% {
              transform: rotateX(30deg);
            }
            100% {
              transform: rotateX(60deg);
            }
          }
          .playing {
            width: 10%;
            height: 100%;
            background-color: #f00;
            border-radius: 50rem;
            transform-origin: bottom;
            animation: informationMove 0.8s ease-in-out infinite;
            &:nth-child(1) {
              animation-delay: -0.1s;
            }
            &:nth-child(2) {
              animation-delay: -0.2s;
            }
            &:nth-child(3) {
              animation-delay: -0.3s;
            }
            &:nth-child(4) {
              animation-delay: -0.4s;
            }
          }
        }
        justify-content: initial;
        width: calc(100% - 30rem);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        .artist {
          font-size: 14rem;
        }
        .album {
          display: block;
          font-size: 12rem;
          transform: scale(0.95);
          flex-shrink: 0;
        }
      }
    }
    .right {
      .clearItem {
        font-size: 25rem;
        cursor: pointer;
        svg{
          width: 16rem;
          height: 16rem;

          margin-right:10rem;
        }
      }
    }
  }
}
</style>
