<template>
  <div class="alApp">
    <img src="../assets/loading.gif" v-show="showLoading" class="loading" alt="" />
    <div class="allCont" v-if="content">
      <i class="fa fa-angle-left" aria-hidden="true" @click="$router.go(-1)"></i>
      <!--  -->
      <div class="albumTop">
        <div
          class="substitute"
          :style="`background-image:url(${content.album.blurPicUrl}?imageView=1&type=webp&thumbnail=100x0)`"
        ></div>
        <div class="leftImg">
          <span>{{ content.album.type }}</span>
          <img
            class="alImg"
            :src="`${content.album.blurPicUrl}?imageView=1&type=webp&thumbnail=250x0`"
            alt=""
          />
          <div class="aljl"></div>
        </div>
        <div class="rightCont">
          <h3>{{ content.album.name }}</h3>
          <div class="songer">
            <span></span>
            <span>{{ content.album.artist.name }}</span>
          </div>
          <div class="publishTime">
            <span></span>
            <span>
              {{ `${new Date(content.album.publishTime).getFullYear()}` }}-
              {{ `${new Date(content.album.publishTime).getMonth() + 1}` }}-
              {{ `${new Date(content.album.publishTime).getDate()}` }}
            </span>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="Introduction">
        <span :class="`${close ? 'close' : 'open'}`" @click="close = !close">
          {{ content.album.description }}
        </span>
        <div class="switchIcon">
          <i
            class="fa"
            :class="`${close ? 'fa-angle-down' : 'fa-angle-up'}`"
            aria-hidden="true"
          ></i>
        </div>
      </div>
      <h3 class="Singlestitle"></h3>
      <!--  -->
      <div class="Top-Singles">
        <ul class="singlesList">
          <li
            class="hot-song"
            @click="
              $emit('Updatd-song', item);
              $emit('song-List', content.songs);
            "
            v-for="(item, index) in content.songs"
            :key="item.id"
          >
            <div class="left">
              {{ index + 1 }}
            </div>
            <div class="right">
              <div class="songLeft">
                <h3>{{ item.name }}</h3>
                <div class="author">
                  <span>
                    <span v-for="name in item.ar" :key="name.name">
                      {{ artistAll(item.ar) }}
                    </span>
                    -
                    {{ item.al.name }}
                  </span>
                </div>
              </div>
              <div class="jl-play"></div>
            </div>
          </li>
        </ul>
      </div>

      <h3 class="Singlestitle" v-if="greatReview.hotComments.length"></h3>
      <!-- hotComments  -->
      <div v-if="greatReview.hotComments.length">
        <div
          class="Comment"
          v-for="item in greatReview.hotComments"
          :key="item.commentId"
        >
          <div class="avatar">
            <div class="avatarBox">
              <img
                :src="`${item.user.avatarUrl}?imageView=1&type=webp&thumbnail=100x0`"
                alt=""
              />
            </div>
          </div>
          <div class="content">
            <div class="con-top">
              <div class="left">
                <div class="avatarName">{{ item.user.nickname }}</div>
                <div class="times">
                  {{ `${new Date(item.time).getFullYear()}` }}{{
                    `${new Date(item.time).getMonth() + 1}`
                  }}{{ `${new Date(item.time).getDate()}` }}
                </div>
              </div>
              <div class="right">
                <div class="dzNum">
                  {{
                    Number(
                      item.likedCount > 100000000
                        ? (item.likedCount / 100000000).toFixed(2) + ""
                        : item.likedCount > 10000
                        ? (item.likedCount / 10000).toFixed(2) + ""
                        : item.likedCount  ) +(loveDz(item.commentId) ? findLoveDz(item.commentId) : 1)
                  }}
                </div>

                <svg
                  @click.stop="clickLoveDz(item.commentId)"
                  v-if="loveDz(item.commentId)"
                  class="u-svg u-svg-unzancmt"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 28 28"
                >
                  <path
                    fill="#999"
                    d="m25.857 14.752c-.015.059-1.506 5.867-2.932 8.813-1.162 2.402-3 2.436-3.099 2.436h-12.826v-13c3 0 5.728-4 5.728-7.275 0-3.725 1.433-3.725 2.142-3.725 1.327 0 1.978 1.345 1.978 4 0 2.872-.832 4.525-.839 4.537-.161.31-.155.682.027.981.181.299.5.482.849.482h6.942c.922 0 1.551.215 1.866.64.467.626.286 1.705.164 2.112m-23.857 10.248v-10c0-1.795.659-1.981.855-2h2.145v13h-2.173c-.829 0-.827-.648-.827-1m25.309-13.54c-.713-.969-1.886-1.46-3.482-1.46h-5.519c.26-.932.519-2.285.519-4 0-5.221-2.507-6-4-6-1.909 0-4.185.993-4.185 5.725 0 2.206-1.923 5.275-3.815 5.275h-4-.011c-1.034.011-2.816.862-2.816 4v10.02c0 1.198.675 2.979 2.827 2.979h16.971.035c.364 0 3.224-.113 4.894-3.564 1.514-3.127 3.01-8.942 3.056-9.14.071-.23.664-2.289-.474-3.836"
                  ></path>
                </svg>

                <svg
                  @click.stop="clickLoveDz(item.commentId)"
                  v-else
                  class="u-svg u-svg-unzancmt"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 28 28"
                >
                  <path
                    fill="rgba(217, 48, 48, 0.8)"
                    d="m25.857 14.752c-.015.059-1.506 5.867-2.932 8.813-1.162 2.402-3 2.436-3.099 2.436h-12.826v-13c3 0 5.728-4 5.728-7.275 0-3.725 1.433-3.725 2.142-3.725 1.327 0 1.978 1.345 1.978 4 0 2.872-.832 4.525-.839 4.537-.161.31-.155.682.027.981.181.299.5.482.849.482h6.942c.922 0 1.551.215 1.866.64.467.626.286 1.705.164 2.112m-23.857 10.248v-10c0-1.795.659-1.981.855-2h2.145v13h-2.173c-.829 0-.827-.648-.827-1m25.309-13.54c-.713-.969-1.886-1.46-3.482-1.46h-5.519c.26-.932.519-2.285.519-4 0-5.221-2.507-6-4-6-1.909 0-4.185.993-4.185 5.725 0 2.206-1.923 5.275-3.815 5.275h-4-.011c-1.034.011-2.816.862-2.816 4v10.02c0 1.198.675 2.979 2.827 2.979h16.971.035c.364 0 3.224-.113 4.894-3.564 1.514-3.127 3.01-8.942 3.056-9.14.071-.23.664-2.289-.474-3.836"
                  ></path>
                </svg>
              </div>
            </div>
            <p>{{ item.content }}</p>
          </div>
        </div>
        <div class="lookAll">
          {{ greatReview.total - greatReview.hotComments.length }}
          <i class="fa fa-angle-right" aria-hidden="true"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      content: "",
      greatReview: "",
      showLoading: true,
      close: true,
      num: 0,
      dzArr: JSON.parse(localStorage.getItem("dzArr")) || [],
    };
  },
  created() {
    let first = this.axios("https://apis.netstart.cn/music/album", {
      params: {
        id: this.$route.query.id,
      },
    });
    let sed = this.axios("https://apis.netstart.cn/music/comment/album", {
      params: {
        id: this.$route.query.id,
      },
    });
    let axiosAjax = Promise.all([first, sed]);
    axiosAjax.then((res) => {
      this.showLoading = false;
      this.content = res[0].data;
      this.greatReview = res[1].data;
    });
  },
  methods: {
    artistAll: function (arr) {
      let arrName = [];
      for (let i = 0; i < arr.length; i++) {
        arrName.push(arr[i].name);
      }
      return arrName.join("/");
    },
    loveDz(zhId) {
      if (!this.dzArr.length) {
        return true;
      } else {
        if (this.dzArr.includes(zhId)) {
          return false;
        } else {
          return true;
        }
      }
    },
    clickLoveDz(dzId) {
      if (!this.dzArr.length) {
        this.dzArr = [dzId, ...this.dzArr];
        localStorage.setItem("dzArr", JSON.stringify(this.dzArr));
        return true;
      } else {
        if (this.dzArr.includes(dzId)) {
          this.dzArr = this.dzArr.filter((o) => o !== dzId);
          localStorage.setItem("dzArr", JSON.stringify(this.dzArr));
          return false;
        } else {
          this.dzArr = [dzId, ...this.dzArr];
          localStorage.setItem("dzArr", JSON.stringify(this.dzArr));
          return true;
        }
      }
    },
    findLoveDz(dzId) {
      if (!this.dzArr.length) {
        return 0;
      } else {
        if (this.dzArr.includes(dzId)) {
          return 1;
        } else {
          return 0;
        }
      }
    },
  },
};
</script>

<style lang="less" scoped>
.flex() {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.alApp {
  background-color: #eaeaea;
}
.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.allCont {
  > i {
    top: 10rem;
    left: 10rem;
    color: #fff;
    font-size: 30rem;
    position: absolute;
    z-index: 99;
  }
  .albumTop {
    display: flex;
    padding: 50rem 15rem 30rem;
    position: relative;
    overflow: hidden;
    .substitute {
      background-position: center;
      background-size: cover;
      position: absolute;
      filter: blur(20rem);
      transform: scale(1.1);
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 0;
    }

    .leftImg {
      position: relative;
      span {
        position: absolute;
        top: 10rem;
        left: 0;
        padding: 2rem 8rem;
        border-radius: 0 50rem 50rem 0;
        background-color: rgba(217, 48, 48, 0.8);
        color: #fff;
      }
      img {
        &.alImg {
          width: 126rem;
        }
      }
      .aljl {
        position: absolute;
        top: 0;
        right: -20rem;
        width: 20rem;
        height: 100%;
        background-image: url("http://localhost:8080/img/jingling.1cf50bdf.png");
        background-position: -2rem -73rem;
        background-size: 430rem;
      }
    }
    .rightCont {
      position: relative;
      z-index: 6;
      margin-left: 25rem;
      padding: 10rem 0;
      h3 {
        font-size: 18rem;
        margin-bottom: 20rem;
        color: #fff;
      }
      .songer {
        font-size: 14rem;
        margin: 10rem 0;
        span:nth-child(1) {
          color: rgba(215, 215, 215, 0.823);
        }
        span:nth-child(2) {
          color: #fff;
        }
      }
      .publishTime {
        font-size: 12rem;
      }
    }
  }
  .Introduction {
    padding: 15rem;
    font-size: 15rem;
    background-color: #fff;
    span.open {
    }
    span.close {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
    }
    .switchIcon {
      text-align: right;
    }
  }
  .Singlestitle {
    padding: 5rem;
    padding-left: 10rem;
    font-size: 12rem;
  }
  .Top-Singles {
    // padding:0 15rem;
    background-color: #fff;
    // margin: 10rem 0;
    display: flex;
    flex-direction: column;
    .singlesList {
      width: 100%;
      li {
        display: flex;
        align-items: center;
        margin: 20rem 0;
      }
      .left {
        flex-shrink: 0;
        font-size: 18rem;
        width: 40rem;
        text-align: center;
        // margin-right: 10rem;
        color: #9c9c9c;
        padding: 10rem;
      }
      .right {
        width: calc(100% - 40rem);
        display: flex;
        justify-content: space-between;
        align-items: center;
        .songLeft {
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          h3 {
            width: calc(100% - 25rem);
            font-size: 18rem;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .author {
            margin-top: 5rem;
            display: flex;
            align-items: center;
            .jl-sq {
              margin-right: 5rem;
              width: 15rem;
              height: 11rem;
              background-image: url(../assets/jingling.png);
              background-size: 180rem;
              background-position: 1rem 2rem;
            }
            span {
              font-size: 12rem;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
        }
        .play {
          .flex();
          justify-content: space-around;
          width: 30rem;
          height: 30rem;
          padding: 3.5rem 3.5rem 3.5rem 0;
          @keyframes informationMove {
            0% {
              transform: rotateX(60deg);
            }
            50% {
              transform: rotateX(30deg);
            }
            100% {
              transform: rotateX(60deg);
            }
          }
          .playing {
            width: 10%;
            height: 100%;
            background-color: #f00;
            border-radius: 50rem;
            transform-origin: bottom;
            animation: informationMove 0.8s ease-in-out infinite;
            &:nth-child(1) {
              animation-delay: -0.1s;
            }
            &:nth-child(2) {
              animation-delay: -0.2s;
            }
            &:nth-child(3) {
              animation-delay: -0.3s;
            }
            &:nth-child(4) {
              animation-delay: -0.4s;
            }
          }
        }
        .jl-play {
          width: 30rem;
          height: 30rem;
          background-image: url(../assets/jingling.png);
          background-size: 179rem;
          background-position: 333rem 0rem;
          flex-shrink: 0;
        }
      }
    }
  }
  .Comment {
    display: flex;
    padding: 10rem;
    background-color: #fff;
    .avatar {
      padding: 0 5rem 0 0;
      .avatarBox {
        width: 35rem;
        // margin: 5rem;
        img {
          // width: 100%;
          border-radius: 50%;
        }
      }
    }
    .content {
      padding: 0 5rem 5rem;
      width: 100%;
      border-bottom: 1rem solid#ccc;
      .con-top {
        display: flex;
        justify-content: space-between;
        .left {
          .avatarName {
            margin-bottom: 5rem;
          }
          .times {
            color: #999;
          }
        }
        .right {
          display: flex;
          align-items: center;
          .dzNum {
            margin-right: 5rem;
            color: #999;
          }
          svg {
            height: 14rem;
          }
        }
      }
      p {
        padding: 5rem 0;
      }
    }
  }
  .lookAll {
    padding: 0 10rem 10rem;
    text-align: center;
    color: #999;
    background-color: #fff;
    font-size: 14rem;
    i {
      font-size: 15rem;
    }
  }
}
</style>
