<template>
  <div class="playApp">
    <img v-show="loadingShow" src="../assets/loading.gif" alt="" class="loading" />
    <div v-if="!loadingShow && songs && playList && detail">
      <i class="fa fa-angle-left back" aria-hidden="true" @click="$router.go(-1)"></i>
      <div class="homeCard-top">
        <div
          class="substitute"
          :style="`background-image:url(${detail?.playlist?.coverImgUrl}?imageView=1&type=webp&thumbnail=100x0)`"
        ></div>
        <div class="homeCard-topBox">
          <div class="homeCard-left">
            <div>
              <img
                class="alImg"
                :src="`${detail?.playlist?.coverImgUrl}?imageView=1&type=webp&thumbnail=100x0`"
                alt=""
              />
              <div class="homeCard-topNum">
                <img src="../assets/music.svg" alt="" />
                <div>{{ $route.query.count }}</div>
              </div>
              <span></span>
            </div>
          </div>
          <div class="homeCard-right">
            <p>{{ detail?.playlist?.name }}</p>
            <div class="artistCont">
              <img
                :src="`${detail?.playlist?.creator?.avatarUrl}?imageView=1&type=webp&thumbnail=100x0`"
                alt=""
              />
              <span>{{ detail?.playlist?.creator?.nickname }}</span>
            </div>
          </div>
        </div>
      </div>
      <h3 class="Singlestitle"></h3>
      <div class="Singles">
        <ul class="singlesList">
          <li
            class="hot-song"
            v-for="(item, index) in songs"
            :key="item.id"
            @click="
              $emit('Updatd-song', item);
              $emit('song-List', songs);
            "
          >
            <div class="left">{{ index + 1 }}</div>
            <div class="right">
              <div class="songLeft">
                <div class="songName">
                  <h3 class="firstName">
                    {{ item.name
                    }}<span class="sedName" v-if="item.alia.length > 0">{{
                      `(${item.alia})`
                    }}</span>
                  </h3>
                </div>

                <div class="author">
                  <span>
                    <span>
                      {{ artistAll(item.ar) }}
                    </span>
                    - {{ item.al.name }}</span
                  >
                </div>
              </div>

              <div class="play" v-if="item.id === nowSong?.id">
                <div class="playing" v-for="(key, index) in 4" :key="index"></div>
              </div>
              <div class="jl-play" v-else></div>
            </div>
          </li>
          <div class="Sing-lookAll">,</div>
        </ul>
      </div>

      <h3 class="Singlestitle"></h3>
      <div>
        <div class="Comment" v-for="item in playList.hotComments" :key="item.commentId">
          <div class="avatar">
            <div class="avatarBox">
              <img
                :src="`${item.user.avatarUrl}?imageView=1&type=webp&thumbnail=375x0`"
                alt=""
              />
            </div>
          </div>
          <div class="content">
            <div class="con-top">
              <div class="left">
                <div class="avatarName">{{ item.user.nickname }}</div>
                <div class="times">
                  {{ `${new Date(item.time).getFullYear()}` }}{{
                    `${new Date(item.time).getMonth() + 1}`
                  }}{{ `${new Date(item.time).getDate()}` }}
                </div>
              </div>
              <div class="right">
                <div class="dzNum">
                  {{
                    Number(
                      item.likedCount > 100000000
                        ? (item.likedCount / 100000000).toFixed(2) + ""
                        : item.likedCount > 10000
                        ? (item.likedCount / 10000).toFixed(2) + ""
                        : item.likedCount  ) +(loveDz(item.commentId) ? findLoveDz(item.commentId) : 1)
                  }}
                </div>
                <!--  -->
                <svg
                  @click.stop="clickLoveDz(item.commentId)"
                  v-if="loveDz(item.commentId)"
                  class="u-svg u-svg-unzancmt"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 28 28"
                >
                  <path
                    fill="#999"
                    d="m25.857 14.752c-.015.059-1.506 5.867-2.932 8.813-1.162 2.402-3 2.436-3.099 2.436h-12.826v-13c3 0 5.728-4 5.728-7.275 0-3.725 1.433-3.725 2.142-3.725 1.327 0 1.978 1.345 1.978 4 0 2.872-.832 4.525-.839 4.537-.161.31-.155.682.027.981.181.299.5.482.849.482h6.942c.922 0 1.551.215 1.866.64.467.626.286 1.705.164 2.112m-23.857 10.248v-10c0-1.795.659-1.981.855-2h2.145v13h-2.173c-.829 0-.827-.648-.827-1m25.309-13.54c-.713-.969-1.886-1.46-3.482-1.46h-5.519c.26-.932.519-2.285.519-4 0-5.221-2.507-6-4-6-1.909 0-4.185.993-4.185 5.725 0 2.206-1.923 5.275-3.815 5.275h-4-.011c-1.034.011-2.816.862-2.816 4v10.02c0 1.198.675 2.979 2.827 2.979h16.971.035c.364 0 3.224-.113 4.894-3.564 1.514-3.127 3.01-8.942 3.056-9.14.071-.23.664-2.289-.474-3.836"
                  ></path>
                </svg>
                <!--  -->
                <svg
                  @click.stop="clickLoveDz(item.commentId)"
                  v-else
                  class="u-svg u-svg-unzancmt"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 28 28"
                >
                  <path
                    fill="rgba(217, 48, 48, 0.8)"
                    d="m25.857 14.752c-.015.059-1.506 5.867-2.932 8.813-1.162 2.402-3 2.436-3.099 2.436h-12.826v-13c3 0 5.728-4 5.728-7.275 0-3.725 1.433-3.725 2.142-3.725 1.327 0 1.978 1.345 1.978 4 0 2.872-.832 4.525-.839 4.537-.161.31-.155.682.027.981.181.299.5.482.849.482h6.942c.922 0 1.551.215 1.866.64.467.626.286 1.705.164 2.112m-23.857 10.248v-10c0-1.795.659-1.981.855-2h2.145v13h-2.173c-.829 0-.827-.648-.827-1m25.309-13.54c-.713-.969-1.886-1.46-3.482-1.46h-5.519c.26-.932.519-2.285.519-4 0-5.221-2.507-6-4-6-1.909 0-4.185.993-4.185 5.725 0 2.206-1.923 5.275-3.815 5.275h-4-.011c-1.034.011-2.816.862-2.816 4v10.02c0 1.198.675 2.979 2.827 2.979h16.971.035c.364 0 3.224-.113 4.894-3.564 1.514-3.127 3.01-8.942 3.056-9.14.071-.23.664-2.289-.474-3.836"
                  ></path>
                </svg>
              </div>
            </div>
            <p>{{ item.content }}</p>
          </div>
        </div>
        <div class="lookAll">
          {{ playList.total - playList.hotComments.length }}
          <i class="fa fa-angle-right" aria-hidden="true"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    nowSong: Object,
  },
  data: function () {
    return {
      loadingShow: true,
      //
      detail: "",
      //
      playList: "",
      //
      songs: "",
      num: 0,
      dzArr: JSON.parse(localStorage.getItem("dzArr")) || [],
      // baisid: 0,
    };
  },
  created() {
    let first = this.axios.get("https://apis.netstart.cn/music/playlist/detail", {
      params: { id: this.$route.query.id },
    });
    let sencond = this.axios.get("https://apis.netstart.cn/music/comment/playlist", {
      params: { id: this.$route.query.id },
    });
    let three = this.axios.get("https://apis.netstart.cn/music/playlist/track/all", {
      params: { id: this.$route.query.id },
    });
    let axiosAjax = Promise.all([first, sencond, three]);
    axiosAjax.then((res) => {
      this.loadingShow = false;
      this.detail = res[0].data;
      this.playList = res[1].data;
      this.songs = res[2].data.songs.splice(0, 10);
    });
  },
  methods: {
    artistAll: function (arr) {
      let arrName = [];
      for (let i = 0; i < arr.length; i++) {
        arrName.push(arr[i].name);
      }
      return arrName.join("/");
    },
    loveDz(zhId) {
      if (!this.dzArr.length) {
        return true;
      } else {
        if (this.dzArr.includes(zhId)) {
          return false;
        } else {
          return true;
        }
      }
    },
    clickLoveDz(dzId) {
      if (!this.dzArr.length) {
        this.dzArr = [dzId, ...this.dzArr];
        localStorage.setItem("dzArr", JSON.stringify(this.dzArr));
        return true;
      } else {
        if (this.dzArr.includes(dzId)) {
          this.dzArr = this.dzArr.filter((o) => o !== dzId);
          localStorage.setItem("dzArr", JSON.stringify(this.dzArr));
          return false;
        } else {
          this.dzArr = [dzId, ...this.dzArr];
          localStorage.setItem("dzArr", JSON.stringify(this.dzArr));
          return true;
        }
      }
    },
    findLoveDz(dzId) {
      if (!this.dzArr.length) {
        return 0;
      } else {
        if (this.dzArr.includes(dzId)) {
          return 1;
        } else {
          return 0;
        }
       
      }
    },
  },
};
</script>

<style lang="less" scoped>
.flex() {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Singlestitle {
  padding: 5rem;
  padding-left: 10rem;
  font-size: 12rem;
}
.playApp {
  background: rgb(242, 242, 242);
}
.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.back {
  position: absolute;
  top: 10rem;
  left: 10rem;
  font-size: 30rem;
  color: #fff;
  z-index: 60;
}
.homeCard-top {
  padding: 50rem 5rem 30rem 15rem;
  position: relative;
  overflow: hidden;
  .substitute {
    background-position: center;
    background-size: cover;
    position: absolute;
    filter: blur(20rem);
    transform: scale(1.1);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 0;
  }
  .homeCard-topBox {
    display: flex;
    position: relative;
    z-index: 9;
    .homeCard-left {
      padding: 0 10rem 0 0;
      > div {
        width: 126rem;
        top: 0;
        right: 0;
        position: relative;
        display: flex;
        align-items: center;
        > img {
          width: 126rem;
        }
        .homeCard-topNum {
          font-size: 12rem;
          top: 0rem;
          padding: 0 3rem;
          width: 100%;
          justify-content: end;
          z-index: 2;
          color: #ffffff;
          background-image: linear-gradient(272deg, rgba(0, 0, 0, 0.173), transparent);
          text-shadow: 0 1rem 1rem rgb(0 0 0 / 59%);
          display: flex;
          align-items: center;
          position: absolute;
          img {
            height: 12rem;
            margin-right: 3rem;
          }
        }
        span {
          position: absolute;
          top: 10rem;
          left: 0;
          z-index: 6;
          padding: 2rem 8rem;
          border-radius: 0 50rem 50rem 0;
          background-color: rgba(217, 48, 48, 0.8);
          color: #fff;
        }
      }
    }
    .homeCard-right {
      padding: 5rem;
      width: 100%;
      p {
        font-size: 18rem;
        color: #fff;
        letter-spacing: 2rem;
        text-indent: 12rem;
        margin-bottom: 3rem;
      }
      .artistCont {
        display: flex;
        align-items: center;
        img {
          border-radius: 50%;
          width: 30rem;
          margin-right: 3rem;
        }
        span {
          color: rgba(255, 255, 255, 0.708);
        }
      }
    }
  }
}
.Singles {
  // padding:0 15rem;
  background-color: #fff;
  // margin: 10rem 0;
  display: flex;
  flex-direction: column;
  .singlesList {
    width: 100%;
    li {
      display: flex;
      align-items: center;
      margin: 10rem 0;
    }
    .left {
      flex-shrink: 0;
      font-size: 18rem;
      padding: 5rem;
      color: #9c9c9c;
      width: 40rem;
      text-align: center;
    }
    .right {
      // padding: 0 10rem;
      width: calc(100% - 40rem);
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1rem solid rgb(204, 204, 204);
      padding-bottom: 10rem;
      .songLeft {
        width: calc(100% - 40rem);
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        .songName {
          font-size: 16rem;
          overflow: hidden;
          width: 100%;
          .firstName {
            width: 100%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            .sedName {
              width: calc(100% - 50rem);
              color: #999;
            }
          }
        }
        .author {
          margin-top: 5rem;
          display: flex;
          align-items: center;
          .jl-sq {
            margin-right: 5rem;
            width: 15rem;
            height: 11rem;
            background-image: url(../assets/jingling.png);
            background-size: 180rem;
            background-position: 1rem 2rem;
          }
          span {
            font-size: 12rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
      }

      .play {
        .flex();
        margin-right: 5rem;
        justify-content: space-around;
        width: 30rem;
        height: 30rem;
        padding: 3.5rem 3.5rem 3.5rem 0;
        @keyframes informationMove {
          0% {
            transform: rotateX(60deg);
          }
          50% {
            transform: rotateX(30deg);
          }
          100% {
            transform: rotateX(60deg);
          }
        }
        .playing {
          width: 10%;
          height: 100%;
          background-color: #f00;
          border-radius: 50rem;
          transform-origin: bottom;
          animation: informationMove 0.8s ease-in-out infinite;
          &:nth-child(1) {
            animation-delay: -0.1s;
          }
          &:nth-child(2) {
            animation-delay: -0.2s;
          }
          &:nth-child(3) {
            animation-delay: -0.3s;
          }
          &:nth-child(4) {
            animation-delay: -0.4s;
          }
        }
      }
      .jl-play {
        width: 30rem;
        height: 30rem;
        background-image: url(../assets/jingling.png);
        background-size: 179rem;
        background-position: 333rem 0rem;
        transform: translateX(-5px);
      }
    }
  }
  .Sing-lookAll {
    margin: 10rem 0;
    font-size: 14rem;
    color: #999;
    text-align: center;
  }
}
.Comment {
  display: flex;
  padding: 10rem;
  background-color: #fff;
  .avatar {
    padding: 0 5rem 0 0;
    .avatarBox {
      width: 30rem;
      // margin: 5rem;
      img {
        // width: 100%;
        border-radius: 50%;
      }
    }
  }
  .content {
    padding: 0 5rem 5rem;
    width: 100%;
    border-bottom: 1rem solid#ccc;
    .con-top {
      display: flex;
      justify-content: space-between;
      .left {
        .avatarName {
          font-size: 14px;
          color: #666;
          line-height: 20px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          -webkit-box-flex: 1;
          -webkit-flex: auto;
          flex: auto;
        }
        .times {
          color: #999;
        }
      }
      .right {
        display: flex;
        align-items: center;
        .dzNum {
          margin-right: 5rem;
          color: #999;
        }
        svg {
          height: 14rem;
        }
      }
    }
    p {
      color: #333;
      font-size: 15px;
      line-height: 22px;
      margin-top: 5px;
    }
  }
}
.lookAll {
  padding: 0 10rem 10rem;
  text-align: center;
  color: #999;
  background-color: #fff;
  font-size: 14rem;
  i {
    font-size: 15rem;
  }
}
</style>
