<template>
  <div class="Artistapp">
    <img class="loading" v-show="showView" src="../assets/loading.gif" alt="" />
    <!-- v-if="!showView" -->
    <div v-if="!showView">
      <div class="ArtistTop">
        <div class="ArtistBox">
          <div class="goBack" @click="$router.go(-1)">
            <i class="fa fa-angle-left" aria-hidden="true"></i>
          </div>
          <img
            :src="`${content?.artist?.cover}?imageView=1&type=webp&thumbnail=751x0`"
            alt=""
          />
          <div class="personalInformation">
            <h3>{{ content?.artist?.name }}</h3>
            <div class="regionalRanking" v-if="content?.artist?.rank?.rank">
               NO.{{ content?.artist?.rank?.rank }}
            </div>
            <div class="nickName" v-if="content?.user?.nickname">
              : {{ content?.user?.nickname }}
            </div>
            <div class="follow-Cnt">
              <span> {{ follow?.followCnt }} </span> |
              <span> {{ `${(follow?.fansCnt / 10000).toFixed(1)}` }}</span>
            </div>
            <div class="residentArtist" v-if="content">
              <span v-if="content?.artist?.identifyTag">{{
                content?.artist?.identifyTag[0]
              }}</span>
              <span v-for="item in content?.artist?.identities" :key="item">{{
                item
              }}</span>
            </div>
          </div>
        </div>
      </div>

      <div class="Introduction" @click="dropDown = !dropDown">
        <h3></h3>
        <p :class="dropDown ? 'Down' : 'closure'">{{ content?.artist?.briefDesc }}</p>
        <div class="down" v-show="!dropDown && content?.artist?.briefDesc.length>200">
           <i class="fa fa-angle-down" aria-hidden="true"></i>
        </div>
        <div class="Up" v-show="dropDown">
          <i class="fa fa-angle-up" aria-hidden="true"></i>
        </div>
      </div>

      <div class="Top-Singles" v-if="songs.length != 0">
        <h3></h3>
        <ul class="singlesList">
          <li
            class="hot-song"
            @click="
              onplayMusic(item);
              playMusic = !playMusic;
              $emit('song-List', songs);
            "
            @Updatd-song="$emit('Updatd-song', $event)"
            v-for="(item, index) in songs"
            :key="item.id"
          >
            <div class="left">
              {{ index + 1 }}
            </div>
            <div class="right">
              <div class="songLeft">
                <h3>{{ item?.name }}</h3>
                <div class="author">
                  <div v-if="item?.sq" class="jl-sq"></div>
                  <span>
                    <span>{{ artistAll(item?.ar) }} </span>
                    -
                    {{ item?.al?.name }}</span
                  >
                </div>
              </div>
              <div class="jl-play"></div>
            </div>
          </li>
        </ul>
      </div>

      <div v-if="newartists">
        <div
          class="newMusic"
          @click="$router.push({ path: '/Album', query: { newArt: newartists } })"
          v-for="item in newartists"
          :key="item.id"
          :class="playMusic ? 'bottom' : ''"
        >
          <h3></h3>
          <div
            class="newMusicBox"
            @click="$router.push({ path: '/Album', query: { id: item?.id } })"
          >
            <div class="left">
              <img class="AlMusic" :src="item?.picUrl" alt="" />
              <div class="jingling"></div>
            </div>
            <div class="content">
              <h3>{{ item?.name }}</h3>
              <h4>{{ item?.artist?.name }}</h4>
              <div class="sizeDate">
                <span class="size">{{ item?.size }}</span>
                <span class="date">
                  {{ `${new Date(item?.publishTime).getFullYear()}` }}-
                  {{
                    `${
                      new Date(item?.publishTime).getMonth() + 1 <= 9
                        ? "0" + (new Date(item?.publishTime).getMonth() + 1)
                        : new Date(item?.publishTime).getMonth() + 1
                    }`
                  }}-
                  {{ `${new Date(item?.publishTime).getDate()}` }}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data: function () {
    return {
      content: "",
      follow: "",
      songs: "",
      newartists: "",
      playMusic: false,
      dropDown: false,
      showView: true,
    };
  },

  created() {
    this.dropDown = false;
    let first = this.axios("https://apis.netstart.cn/music/artist/detail", {
      params: {
        id: this.$route.query.id,
      },
    });
    let sed = this.axios("https://apis.netstart.cn/music/artist/follow/count", {
      params: {
        id: this.$route.query.id,
      },
    });
    let three = this.axios("https://apis.netstart.cn/music/artist/top/song", {
      params: {
        id: this.$route.query.id,
      },
    });
    let four = this.axios("https://apis.netstart.cn/music/album/newest");
    let AllAjax = Promise.all([first, sed, three, four]);
    AllAjax.then((res) => {
      this.showView = false;
      // console.log(res);
      this.content = res[0]?.data?.data;
      this.follow = res[1]?.data?.data;
      this.songs = res[2]?.data?.songs.splice(0, 5);
      this.newartists = res[3]?.data?.albums.filter(
        (o) => o.artist.id == this.$route.query.id
      );
    });
  },
  methods: {
    ajaxQuanjiatong: function ({ id }) {
      let first = this.axios("https://apis.netstart.cn/music/artist/detail", {
        params: {
          id,
        },
      });
      let sed = this.axios("https://apis.netstart.cn/music/artist/follow/count", {
        params: {
          id,
        },
      });
      let three = this.axios("https://apis.netstart.cn/music/artist/top/song", {
        params: {
          id,
        },
      });
      let four = this.axios("https://apis.netstart.cn/music/album/newest");
      let AllAjax = Promise.all([first, sed, three, four]);
      return AllAjax;
    },
    onplayMusic: function (res) {
      this.$emit("Updatd-song", res);
    },
    artistAll: function (arr) {
      let arrName = [];
      for (let i = 0; i < arr.length; i++) {
        arrName.push(arr[i].name);
      }
      return arrName.join("/");
    },
  },
  beforeRouteUpdate(to,from,next) {
    this.showView = true;
    this.ajaxQuanjiatong({ id: to.query.id }).then((res) => {
      this.showView = false;
      console.log(res);
      this.content = res[0]?.data?.data;
      this.follow = res[1]?.data?.data;
      this.songs = res[2]?.data?.songs.splice(0, 5);
      this.newartists = res[3]?.data?.albums.filter(
        (o) => o.artist.id == to.query.id
      );
      next()
    });
  },
};
</script>

<style lang="less" scoped>
.flex() {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
* {
  margin: 0;
  padding: 0;
}
.Artistapp {
  background: #f6f6f6;
  .loading {
    position: absolute;
    z-index: 9999;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
.ArtistTop {
  background-color: #fff;
  .ArtistBox {
    position: relative;
    &::after {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgb(0 0 0 / 15%);
      content: "";
      z-index: 0;
      top: -3rem;
    }
    .goBack {
      position: absolute;
      top: 0;
      left: 0;
      padding: 15rem;
      color: #fff;
      font-size: 20rem;
      z-index: 2;
    }
    img {
      width: 100%;
      min-height: 250rem;
      font-size: 0;
    }
    .personalInformation {
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 15rem;
      color: #fff;
      z-index: 1;
      h3 {
        font-size: 20rem;
        margin: 8rem 0;
      }
      .regionalRanking {
        font-size: 13rem;
        margin: 15rem 0;
      }
      .nickName {
        margin: 5rem 0;
        color: rgb(255, 255, 255);
        font-size: 13rem;
        transform: scale(0.9);
        transform-origin: left;
      }
      .follow-Cnt {
        margin: 5rem 0;
        font-size: 14rem;
        color: rgb(255, 255, 255);
      }
      .residentArtist {
        margin: 5rem 0;
        display: flex;
        align-items: center;
        color: rgb(231, 231, 231);
        span {
          border: 1px solid rgb(194, 194, 194);
          padding: 1rem;
          color: rgb(194, 194, 194);
          border-radius: 3rem;
          margin: 0 -2rem;
          transform: scale(0.7);
          transform-origin: left;
        }
      }
    }
  }
}
.Introduction {
  padding: 15rem;
  background-color: #fff;

  h3 {
    font-size: 18rem;
    font-weight: bold;
    margin-bottom: 15rem;
  }
  p {
    font-size: 14rem;
    color: rgb(82, 82, 82);
    &.closure {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
    }
    &.Down {
    }
  }
  div.down,
  div.Up {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12.5rem;
    margin-top: 20rem;
    i {
      margin-left: 5rem;
    }
  }
}
.Top-Singles {
  padding: 15rem;
  background-color: #fff;
  margin: 10rem 0;
  display: flex;
  flex-direction: column;
  > h3 {
    font-size: 18rem;
    font-weight: bold;
  }
  .singlesList {
    width: 100%;
    li {
      display: flex;
      align-items: center;
      margin: 20rem 0;
    }
    .left {
      flex-shrink: 0;
      font-size: 18rem;
      // width: 40rem;
      text-align: center;
      // margin-right: 10rem;
      color: #9c9c9c;
      padding: 10rem 10rem 10rem 0;
    }
    .right {
      padding: 0 10rem;
      width: calc(100% - 20rem);
      display: flex;
      justify-content: space-between;
      align-items: center;
      .songLeft {
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        h3 {
          width: calc(100% - 25rem);
          font-size: 16rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .author {
          margin-top: 5rem;
          display: flex;
          align-items: center;
          .jl-sq {
            margin-right: 5rem;
            width: 15rem;
            height: 11rem;
            background-image: url(../assets/jingling.png);
            background-size: 180rem;
            background-position: 1rem 2rem;
          }
          span {
            font-size: 12rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
      }
      .play {
        .flex();
        justify-content: space-around;
        width: 30rem;
        height: 30rem;
        padding: 3.5rem 3.5rem 3.5rem 0;
        @keyframes informationMove {
          0% {
            transform: rotateX(60deg);
          }
          50% {
            transform: rotateX(30deg);
          }
          100% {
            transform: rotateX(60deg);
          }
        }
        .playing {
          width: 10%;
          height: 100%;
          background-color: #f00;
          border-radius: 50rem;
          transform-origin: bottom;
          animation: informationMove 0.8s ease-in-out infinite;
          &:nth-child(1) {
            animation-delay: -0.1s;
          }
          &:nth-child(2) {
            animation-delay: -0.2s;
          }
          &:nth-child(3) {
            animation-delay: -0.3s;
          }
          &:nth-child(4) {
            animation-delay: -0.4s;
          }
        }
      }
      .jl-play {
        width: 30rem;
        height: 30rem;
        background-image: url(../assets/jingling.png);
        background-size: 179rem;
        background-position: 333rem 5rem;
      }
    }
  }
}
.newMusic {
  background: #fff;
  border-bottom: 1rem solid #cdcdcd;
  margin: 5rem 0;
  &.bottom {
    margin-bottom: 60rem;
  }
  padding: 15rem;
  > h3 {
    font-size: 18rem;
    font-weight: bold;
    padding: 10rem 0;
  }
  .newMusicBox {
    display: flex;
    align-items: center;
    .left {
      display: flex;
      .AlMusic {
        height: 80rem;
      }
      .jingling {
        width: 15rem;
        height: 80rem;
        background-image: url(../assets/jingling.png);
        background-position: -1rem -50rem;
        background-size: 282px;
        // background-size: 100%;
      }
    }

    .content {
      margin-left: 15rem;
      h3 {
        font-size: 15rem;
      }
      h4 {
        font-size: 13rem;
        margin-top: 3rem;
        margin-bottom: 10rem;
        color: rgb(121, 121, 121);
      }
      .sizeDate {
        font-size: 13rem;
        color: rgb(121, 121, 121);
      }
    }
  }
}
</style>
