<template>
  <div v-if="song" :class="openPage ? '' : 'PlayPage'">
    <div class="songPage" :class="openPage ? '' : 'close'">
      <!--    -->
      <div
        class="substitute"
        :style="`background-image:url(${
          song?.picUrl || song?.al.picUrl
        }?imageView=1&type=webp&thumbnail=200x0)`"
      >
        <div class="mark"></div>
      </div>
      <!--  -->
      <div class="songPage-top">
        <i
          @click.stop="
            $emit('open-Page');
            playBody();
            lyricsShow = false;
          "
          class="fa fa-angle-down"
          aria-hidden="true"
        ></i>
        <div class="song-cont">
          <div class="songName" style="text-align: center">{{ song.name }}</div>
          <!-- class="songAirst" -->
          <div class="songAirstBox" style="display: flex">
            <div
              @click.stop="
                song?.song?.album?.artists
                  ? song?.song?.album?.artists.length > 1
                    ? (markClose = false)
                    : ''
                  : song?.ar.length > 1
                  ? (markClose = false)
                  : ''
              "
            >
              {{ artistAll(song?.song?.album?.artists ?? song?.ar) }}
              <svg
                v-if="
                  song?.song?.album?.artists
                    ? song?.song?.album?.artists.length > 1
                    : song?.ar.length > 1
                "
                t="1665763725596"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="3417"
                width="48"
                height="48"
              >
                <path
                  d="M383.291616 808.954249c-5.175883 0-10.353812-1.950422-14.338566-5.862521-8.064676-7.919367-8.182356-20.877493-0.26299-28.942169l273.602402-278.620695L368.69006 216.907145c-7.919367-8.064676-7.801686-21.022803 0.26299-28.942169 8.065699-7.918343 21.022803-7.80271 28.942169 0.26299l287.685141 292.960285c7.818059 7.961322 7.818059 20.717857 0 28.67918L397.895219 802.826692C393.887952 806.907637 388.591319 808.954249 383.291616 808.954249z"
                  p-id="3418"
                  fill="#dbdbdbb8"
                ></path>
              </svg>
              <span class="guanzhu" v-else></span>
            </div>
          </div>
        </div>
        <div class="songShare">
          <img class="iconImg" src="../assets/songShare-bai-48.svg" alt="" />
        </div>
      </div>
      <!--  -->
      <div class="centerImg" v-if="!lyricsShow" @click="lyricsShow = true">
        <img
          class="zhizhen"
          :class="playMusic ? '' : 'playNull'"
          src="../assets/needle.png"
          alt=""
        />
        <img
          class="zhuanpan"
          :class="playMusic ? 'imgRotute' : ''"
          :src="`${
            song?.picUrl || song?.al.picUrl
          }?imageView=1&type=webp&thumbnail=200x0`"
          alt=""
        />
      </div>
      <LyricsView
        v-if="lyricsShow"
        @click.native="lyricsShow = false"
        :lyricsShow="lyricsShow"
        :updateTime="updateTime"
        :duration="duration"
        :songId="song.id"
      />
      <!--  -->
      <div class="songPageFooter">
        <ul class="Miscellaneous">
          <li>
            <img
              v-if="loveSong(song.id)"
              @click.stop="clickLoveSong(song.id)"
              src="../assets/songLove-bai-48.svg"
              alt=""
            />
            <img
              v-else
              @click.stop="clickLoveSong(song.id)"
              src="../assets/songLove-red-48.svg"
              alt=""
            />
          </li>
          <li><img src="../assets/songXiaZai-bai-48.svg" alt="" /></li>
          <li><img src="../assets/songSing-bai-48.svg" alt="" /></li>
          <li><img src="../assets/songPingLun-bai-48.svg" alt="" /></li>
          <!-- <li><img src="../assets/song" alt=""></li> -->
        </ul>
        <!--  -->
        <div class="progressBar">
          <div class="nowTime">{{ songTimes(value) }}</div>
          <div class="rangeBar">
            <input
              type="range"
              name=""
              id=""
              ref="input"
              min="0"
              v-model="value"
              @input="draging = true"
              :max="duration"
              @change="
                draging = false;
                $emit('upDate-time', $event.target.value);
              "
            />
            <div class="mark">
              <div class="markBox" :style="`width:${(value / duration) * 100}%`"></div>
            </div>
            <div
              class="stars"
              :class="playMusic ? 'move' : ''"
              :style="`left:calc(${(value / duration) * 100}% - 5rem)`"
            ></div>
          </div>
          <div class="endTime">{{ songTimes(duration) }}</div>
        </div>
        <!--  -->
        <div class="controls">
          <div class="loop" @click="$emit('toggle-Status', nowStatus)">
            <img
              class="iconImg"
              v-show="nowStatus == ''"
              src="../assets/songAllLoop-bai-48.svg"
              alt=""
            />
            <img
              class="iconImg"
              v-show="nowStatus == ''"
              src="../assets/songoldLoop-bai-48.svg"
              alt=""
            />
            <img
              class="iconImg"
              v-show="nowStatus == ''"
              src="../assets/songRandom-bai-48.svg"
              alt=""
            />
          </div>
          <div class="songstutas">
            <div
              class="up"
              @click.stop="
                songList && songList.length < 1
                  ? ($emit('open-Page'), playBody(), (lyricsShow = false))
                  : songList.length >= 1
                  ? $emit('change-song', 'perv')
                  : ''
              "
            >
              <img class="iconImg" src="../assets/songUp-bai-48.svg" alt="" />
            </div>
            <div class="play" v-show="!playMusic" @click.stop="$emit('Update-play')">
              <img src="../assets/songPlay-bai-48.svg" alt="" />
            </div>
            <div class="play" v-show="playMusic" @click.stop="$emit('Update-stop')">
              <img src="../assets/songPause-bai-48.svg" alt="" />
            </div>
            <div
              class="down"
              @click.stop="
                songList && songList.length < 1
                  ? ($emit('open-Page'), playBody(), (lyricsShow = false))
                  : songList.length >= 1
                  ? $emit('change-song', 'next')
                  : ''
              "
            >
              <img class="iconImg" src="../assets/songDown-bai-48.svg" alt="" />
            </div>
          </div>
          <div class="songList" @click.stop="$emit('show-List')">
            <img class="iconImg" src="../assets/songList-bai-48.svg" alt="" />
          </div>
        </div>
      </div>

      <div
        class="artistMark"
        :class="{ close: markClose }"
        v-if="song?.song?.album?.artists?.length > 1 || song?.ar?.length > 1"
      >
        <ul class="artistList">
          <div class="allArtist"></div>
          <li
            v-for="(item, i) in song?.song?.album?.artists ?? song?.ar"
            :key="i"
            @click="
              $router.push({ path: '/Artist', query: { id: item.id } });
              markClose = true;
              $emit('open-Page');
              playBody();
            "
          >
            <!-- {{ item }} -->
            <div class="left">
              <img :src="item.picUrl ?? '1.png'" alt="" />
              <!-- {{item}} -->
              <div>{{ item.name }}</div>
            </div>
            <div class="right"><span class="sp">+</span> </div>
          </li>
        </ul>
      </div>

      <transition name="showS">
        <div class="showStatus" v-show="showStatus">
          {{ nowStatus }}
        </div>
      </transition>
      <div class="mark" @click="markClose = true" :class="{ close: markClose }"></div>
    </div>
  </div>
</template>

<script>
import LyricsView from "@/components/lyricsView.vue";
var _ = require("lodash");
export default {
  components: {
    LyricsView,
  },
  props: {
    song: Object,
    playMusic: Boolean,
    openPage: Boolean,
    updateTime: [Number, String],
    duration: [Number, String],
    loopStatus: Array,
    nowStatus: String,
    songList: Array,
  },
  data: function () {
    return {
      lyricsShow: false,
      lastTime: 0,
      draging: false,
      tmpCurrentTime: this.updateTime,
      showStatus: false,
      value: this.updateTime,
      markClose: true,
      bais: false,
      loveArr: JSON.parse(localStorage.getItem("loveArr")) || [],
    };
  },

  methods: {
    songTimes(time) {
      let min = Math.floor(time / 60);
      let sed = Math.floor(time % 60);
      return `${min < 10 ? `0${min}` : min}:${sed < 10 ? `0${sed}` : sed}`;
    },
    changeStatus: function () {
      this.showStatus = false;
    },
    playBody: function () {
      document.querySelector("body").style.overflow = "auto";
    },
    artistAll: function (arr) {
      let arrName = [];
      for (let i = 0; i < arr.length; i++) {
        arrName.push(arr[i].name);
      }
      return arrName.join(" / ");
    },
    //
    // true true    false  
    loveSong(songId) {
      if (!this.loveArr.length) {
        return true;
      } else {
        // console.log("");
        if (this.loveArr.includes(songId)) {
          return false;
        } else {
          return true;
        }
      }
    },
    clickLoveSong(songId) {
      if (!this.loveArr.length) {
        this.loveArr = [songId, ...this.loveArr];
        localStorage.setItem("loveArr", JSON.stringify(this.loveArr));
        return true;
      } else {
        if (this.loveArr.includes(songId)) {
          this.loveArr = this.loveArr.filter((o) => o !== songId);
          localStorage.setItem("loveArr", JSON.stringify(this.loveArr));
          return false;
        } else {
          this.loveArr = [songId, ...this.loveArr];
          localStorage.setItem("loveArr", JSON.stringify(this.loveArr));
          return true;
        }
      }
    },
  },
  created() {
    // this.bais = this.loveSong(this.song.id);
    this.debounceWhileSearch = _.debounce(this.changeStatus, 800);
  },
  watch: {
    updateTime: function () {
      if (!this.draging) {
        this.value = this.updateTime;
        // console.log("false")
      }
      // else{
      // console.log("true")
      // }
    },
    nowStatus: function () {
      // console.log("nowStatus");
      this.showStatus = true;
      this.debounceWhileSearch();
    },
  },
};
</script>

<style lang="less" scoped>
.mark {
  &.close {
    opacity: 0;
    visibility: hidden;
  }
  opacity: 1;
  transition: all 0.3s;
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 98;
  width: 100%;
  height: 100vh;
  background-color: rgb(0 0 0 / 51%);
}
.artistMark {
  position: absolute;
  width: 100%;
  bottom: 0;
  z-index: 99;
  border-top-left-radius: 20rem;
  border-top-right-radius: 20rem;
  background-color: #1e2746;
  transition: all 0.3s;
  opacity: 1;
  transform: translateY(0);
  &.close {
    transform: translateY(100%);
    opacity: 0;
    visibility: hidden;
  }
  .artistList {
    width: 100%;
    .allArtist {
      width: 100%;
      border-bottom: 1rem solid #ccc;
      padding: 20rem 15rem;
      font-size: 13rem;
    }
    li {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 10rem 15rem;
      .left {
        img {
          width: 40rem;
          height: 40rem;
          border-radius: 50%;
        }
        div {
          margin-left: 10rem;
        }
        display: flex;
        align-items: center;
      }
      .right {
        .sp {
          font-size: 16rem;
          margin-right: 5rem;
        }
        border-radius: 50rem;
        border: 1rem solid #e0cfc5;
        color: #e0cfc5;
        padding: 0 10rem;
        display: flex;
        align-items: center;
      }
    }
  }
}
//showS
.showS-enter-active,
.showS-leave-active {
  transition: opacity 0.5s;
}
.showS-enter,
.showS-leave-to {
  opacity: 0;
}

.flex() {
  display: flex;
  align-items: center;
}
.tranAll() {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 12;
}
.iconImg {
  width: 30rem;
}

.PlayPage {
  overflow: hidden;
  .songPage {
    &.close {
      top: 100%;
    }
  }
}
.songPage {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.588);
  z-index: 299;
  transition: all 0.5s;
  color: #fff;
  overflow: hidden;
  .showStatus {
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(255, 255, 255, 0.531);
    color: #000;
    border-radius: 50rem;
    padding: 10rem 25rem;
    z-index: 999;
  }
  .substitute {
    background-position: center;
    background-size: cover;
    position: absolute;
    filter: blur(10rem) brightness(0.5);
    transform: scale(1.1);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 0;
    .mark {
      position: absolute;
      .tranAll();
      width: 100%;
      height: 100vh;
      background-color: rgba(0, 0, 0, 0.504);
      z-index: 0;
    }
  }
  .songPage-top {
    position: relative;
    z-index: 12;
    padding: 20rem 15rem;
    .flex();
    justify-content: space-between;
    .song-cont {
      width: calc(100% - 40rem);
      overflow: hidden;
      .songName {
        text-align: center;
        padding: 0 15rem;
        white-space: nowrap;
        font-size: 15rem;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .songAirstBox {
        display: flex;
        > div {
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          margin: 0 auto;
          align-items: center;
          font-size: 13rem;
          color: #dbdbdbb8;
          display: flex;
          padding: 5rem 0;
          svg {
            width: 20rem;
            height: 20rem;
          }
          .guanzhu {
            padding: 0 3rem;
            transform-origin: top;
            transform: scale(0.8);
            border-radius: 4rem;
            border: 1rem solid #dbdbdb5b;
            color: #dbdbdbb8;
          }
        }
      }
    }
    i {
      color: #fff;
      font-size: 30rem;
      z-index: 999;
    }
  }
  @keyframes songImgRotute {
    from {
      transform: translate(-50%, -50%) rotate(0);
    }
    to {
      transform: translate(-50%, -50%) rotate(360deg);
    }
  }
  .centerImg {
    position: relative;
    z-index: 12;
    min-height: 350rem;
    position: relative;
    top: 25rem;
    animation-delay: 1s;
    display: flex;
    justify-content: center;
    align-items: self-start;
    &::after {
      content: "";
      border-radius: 50%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(1.05);
      width: 250rem;
      height: 250rem;
      background-color: rgba(255, 255, 255, 0.307);
      position: absolute;
    }
    .zhizhen {
      position: relative;
      width: 84rem;
      z-index: 13;
      top: -20rem;
      left: 0;
      transform: translateX(50%);
      transition: all 0.3s;
      transform: rotate(348deg) translateX(25%);
      &.playNull {
        transform: rotate(326deg) translateX(25%);
      }
      transform-origin: 33rem 10rem;
    }
    .zhuanpan {
      animation: songImgRotute 8s linear infinite;
      animation-play-state: paused;
      border: 40rem solid #000;
      .tranAll();
      width: 250rem;
      height: 250rem;
      border-radius: 50%;
      &.imgRotute {
        animation: songImgRotute 8s linear infinite;
        animation-play-state: running;
      }
    }
  }
  .songPageFooter {
    position: absolute;
    z-index: 12;
    bottom: 0;
    width: 100%;
    padding: 15rem 15rem 5rem;
    .Miscellaneous {
      padding: 20rem 15rem;
      // margin-top: 85rem;
      .flex();
      justify-content: center;
      li {
        width: 25rem;
        margin: 0 20rem;
      }
    }
    .progressBar {
      width: 100%;
      .flex();
      padding: 10rem 5rem;
      justify-content: center;
      align-items: center;
      .nowTime {
        flex-shrink: 0;
        margin-right: 10rem;
      }
      .rangeBar {
        width: 100%;
        position: relative;
        display: flex;
        input {
          width: 100%;
          opacity: 0;
          z-index: 99;
          position: relative;
        }
        .mark {
          .tranAll();
          width: 100%;
          height: 1rem;
          background-color: rgb(149, 149, 149);
          border-radius: 50rem;
          position: absolute;
          z-index: 0;
          .markBox {
            position: absolute;
            left: 0;
            width: 0%;
            height: calc(100%);
            background-color: rgb(45, 45, 45);
            border-radius: 50rem;
            position: absolute;
            z-index: 1;
            // &::after {
            //   .tranAll();
            //   content: "";
            //   width: 5rem;
            //   height: 5rem;
            //   border-radius: 50%;
            //   background-color: #fff;
            // }
          }
        }
        @keyframes flicker {
          0% {
            background-color: rgb(255, 255, 255);
          }
          50% {
            background-color: rgb(0, 0, 0);
          }
          100% {
            background-color: rgb(255, 255, 255);
          }
        }
        .stars {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          width: 8rem;
          height: 8rem;
          border-radius: 50%;
          background-color: #fff;
          &.move {
            animation: flicker 2s linear infinite reverse;
          }
        }
      }
      .endTime {
        margin-left: 10rem;
        flex-shrink: 0;
      }
    }
    .controls {
      padding: 15rem;
      .flex();
      justify-content: space-between;
      .loop {
        width: 30rem;
        flex-shrink: 0;
      }
      .songstutas {
        .flex();
        > div {
          margin: 0 5rem;
        }
        .up {
        }
        .play {
        }
        .down {
        }
      }
      .songList {
        width: 30rem;
        flex-shrink: 0;
      }
    }
  }
}
</style>
