<template>
  <div class="search">
    <!--  -->
    <div class="searchTab">
      <!--  input -->
      <input
        type="text"
        placeholder="//"
        v-model="value"
        @input="oninput()"
        @focus="
          oninput();
          whileSearchFunc(value);
          whileShow = false;
        "
        @keyup.enter="searchEnter"
        ref="searchInput"
      />
      <img src="../assets/search.svg" alt="" />
      <img
        @click="
          value = '';
          whileSearchArr = null;
          endSong = null;
        "
        v-show="value"
        src="../assets/killValue.svg"
        alt=""
      />
    </div>
    <!--  -->
    <div class="hotLoading" v-if="hotShow">
      <img src="../assets/loading.gif" alt="" />
    </div>
    <div v-else-if="!hotShow && !value">
      <!--  -->
      <ul class="hotSearch">
        <h5></h5>
        <HotSearch
          v-for="item in hotSearch"
          @Hot-Click="hotsearClick(item.first)"
          :key="item.first"
          :hotSearch="item"
        />
      </ul>
      <!--  -->
      <ul class="searchHistory">
        <SearchHistory
          v-for="item in searchHistory"
          :key="item.id"
          :history="item"
          @History-Value="changeValue"
          @Kill-Record="killSearchHistory"
        />
      </ul>
    </div>
    <!--  -->
    <div class="whileLoading" v-show="whileShow">
      <img src="../assets/loading.gif" alt="" />
    </div>
    <!-- value && endSong.length == 0 -->
    <ul class="whileSearching" v-if="whileSearchArr && !resShow">
      <h3 @click="whileClick(value)" v-if="whileSearchArr.length > 0">
        {{ value }}
      </h3>
      <WhileSearching
        v-for="item in whileSearchArr"
        :key="item?.keyword"
        :whileValue="item"
        @searitem-click="whileClick"
      />
    </ul>

    <div class="resLoading" v-show="resShow">
      <img src="../assets/loading.gif" alt="" />
    </div>
    <!--  v-show="value && endSong.length !== 0"-->
    <ul class="resultSearch" v-if="endSong">
      <h4 v-show="songArtist || songAlbum"></h4>
      <!--  -->
      <li
        class="Artist"
        v-if="songArtist"
        @click="$router.push({ path: '/Artist', query: { id: songArtist[0]?.id } })"
      >
        <div class="ArtistBox">
          <div class="ArtLeft">
            <img
              :src="`${songArtist[0]?.img1v1Url}?imageView=1&type=webp&thumbnail=100x0`"
              alt=""
            />
            <div class="content">
              <div>
                {{ songArtist[0]?.occupation }}:
                {{ `${songArtist[0]?.name} ${songArtist[0]?.alias[0] ? (songArtist[0]?.alias[0]) : ''   }` }}
              </div>
            </div>
          </div>
          <div class="GoArtist">
            <i class="fa fa-angle-right" aria-hidden="true"></i>
          </div>
        </div>
      </li>
      <!--  -->
      <li
        class="Album"
        v-if="songAlbum"
        @click="$router.push({ path: '/Album', query: { id: songAlbum[0]?.id } })"
      >
        <div class="AlbumBox">
          <div class="AlLeft">
            <img
              :src="`${songAlbum[0]?.picUrl}?imageView=1&type=webp&thumbnail=100x0`"
              alt=""
            />
            <div class="content">
              <div>{{ songAlbum[0]?.type }}:{{ songAlbum[0]?.name }}</div>
              <div>{{ songAlbum[0]?.artist.name }}</div>
            </div>
          </div>
          <div class="GoAlbum">
            <i class="fa fa-angle-right" aria-hidden="true"></i>
          </div>
        </div>
      </li>
      <SearchEnd
        v-for="song in endSong.songs"
        @Updatd-song="$emit('Updatd-song', $event)"
        @click.native="$emit('song-List',endSong.songs)"
        :key="song.id"
        :song="song"
        :nowSong="nowSong"
      />
    </ul>
  </div>
</template>
<script>
import HotSearch from "@/components/HotSearch.vue";
import SearchHistory from "@/components/SearchHistory.vue";
import WhileSearching from "@/components/whileSearching.vue";
import SearchEnd from "@/components/SearchEnd.vue";
var _ = require("lodash");
export default {
  props: ["Appsong",'nowSong'],
  // name: "HomeView",
  components: {
    HotSearch,
    SearchHistory,
    WhileSearching,
    SearchEnd,
  },

  data: function () {
    return {
      // loading
      hotShow: true,
      whileShow: false,
      resShow: true,
      // EndSearch: false,
      value: "",
      hotSearch: "",
      whileSearchArr: null,
      endSong: null,
      searchHistory: JSON.parse(window.localStorage.getItem("History")) || [],
      songAlbum: null,
      songArtist: null,
      // songNewMlog: null,
    };
  },

  methods: {
    // (ok)
    oninput: function () {
      this.endSong = null;
    },
    //(ok)
    searchEnter: function (event) {
      this.whileSearchArr = null;
      this.searchDone();
      this.$refs.searchInput.blur();

      let Hstvalue = event.target.value;
      this.searchHistory = [...new Set([Hstvalue, ...this.searchHistory])];
      this.searchHistory = this.searchHistory.splice(0, 10);
    },
    // 
    whileSearchFunc: function (keywords) {
      if (this.value && !this.endSong) {
        this.whileSearchArr = null;
        this.showView = true;
        this.axios("https://apis.netstart.cn/music/search/suggest", {
          params: {
            keywords,
            type: "mobile",
          },
        }).then((res) => {
          // this.whileShow = true;
          this.whileSearchArr = res?.data?.result?.allMatch;
        });
      }
    },
    //
    hotsearClick: function (res) {
      this.value = res;
      this.searchDone();
      this.searchHistory = [...new Set([res, ...this.searchHistory])];
      this.searchHistory = this.searchHistory.splice(0, 10);
    },
    //Value
    changeValue: function (res) {
      this.value = res;
      this.searchDone();
      this.searchHistory = [...new Set([res, ...this.searchHistory])];
      this.searchHistory = this.searchHistory.splice(0, 10);
    },
    //
    killSearchHistory: function (res) {
      this.searchHistory = this.searchHistory.filter((o) => {
        return o !== res;
      });
    },
    // 
    searchDone: function () {
      //
      this.resShow = true;
      this.axios("https://apis.netstart.cn/music/cloudsearch", {
        params: {
          keywords: this.value,
          limit: 30,
          offset: 0,
        },
      }).then((res) => {
        this.resShow = false;
        this.whileSearchArr = null;
        this.value ? (this.endSong = res.data.result) : (this.endSong = null);
      });
      // or 
      this.axios("https://apis.netstart.cn/music/search/multimatch", {
        params: {
          keywords: this.value,
        },
      }).then((res) => {
        this.songAlbum = res.data.result?.album;
        this.songArtist = res.data.result?.artist;
      });
    },
    //
    whileClick: function (res) {
      this.value = res;
      this.searchDone();
      this.searchHistory = [...new Set([res, ...this.searchHistory])];
    },
  },
  created() {
    this.whileShow=false,
    this.resShow=false,

    this.hotShow = true;
    this.axios("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.hotShow = false;
      this.hotSearch = res.data.result.hots;
    });
    this.debounceWhileSearch = _.debounce(this.whileSearchFunc, 800);
  },
  watch: {
    //
    searchHistory: function (res) {
      // console.log("");
      window.localStorage.setItem("History", JSON.stringify(res));
    },
    //
    value: function (n) {
      // this.EndSearch = false;
      if (n) {
        this.debounceWhileSearch(n);
      } else {
        this.whileSearchArr = [];
      }
    },
  },
};
</script>

<style lang="less" scoped>
.hotLoading,
.whileLoading,
.resLoading {
  width: 100%;
  min-height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.flex() {
  display: flex;
  align-items: center;
  justify-content: center;
}

.hotSearch {
  display: flex;
  flex-wrap: wrap;
  padding: 15rem 10rem 0;

  h5 {
    width: 100%;
    font-size: 12rem;
    padding: 0 10rem 5rem;
  }
}
.search {
  .searchTab {
    position: relative;
    padding: 15rem 10rem;
    box-shadow: 0 1rem 2rem 0 #0000001f;
    img {
      width: 15rem;
      height: 15rem;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      &:nth-child(2) {
        left: 20rem;
      }
      &:nth-child(3) {
        right: 20rem;
      }
    }
    input {
      width: 100%;
      border-radius: 50rem;
      outline: 0;
      border: 0;
      background-color: rgb(233, 233, 233);
      padding: 8rem 15rem 8rem 35rem;
      &::placeholder {
        // padding-left: 25rem;
        color: rgb(201, 201, 201);
      }
    }
    .flex();
  }
}
.whileSearching {

  h3 {
    padding: 15rem 15rem 15rem 0;
    margin-left: 15rem;
    font-size: 15rem;
    color: #507daf;
    border-bottom: 1px solid rgb(136 136 136 / 15%);
  }
}
.resultSearch {
  position: absolute;
  top: 200rem;
  background-color: #fff;
  min-height: 500rem;
  padding-bottom: 60rem;
  width: 100%;
  h4 {
    font-size: 12rem;
    padding: 0 10rem;
    margin: -10rem 0;
    color: #666;
  }
  .Artist {
    padding: 10rem;
    .ArtistBox {
      display: flex;
      justify-content: space-between;
      border-bottom: 1rem solid #ccc;
      padding: 5rem 0;
      .ArtLeft {
        display: flex;
        img {
          width: 50rem;
        }
        .content {
          // width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 18rem;
          margin-left: 10rem;
        }
      }
      .GoArtist {
        display: flex;
        align-items: center;
        font-size: 25rem;
      }
    }
  }
  .Album {
    padding: 10rem;
    .AlbumBox {
      display: flex;
      justify-content: space-between;
      border-bottom: 1rem solid #ccc;
      padding: 5rem 0;
      .AlLeft {
        display: flex;
        h4 {
        }
        img {
          width: 50rem;
        }
        .content {
          // width: 100%;
          display: flex;
          flex-direction: column;
          font-size: 15rem;
          margin-left: 10rem;
          div:nth-child(1) {
            font-size: 18rem;
            font-weight: 500;
          }
        }
      }
      .GoAlbum {
        display: flex;
        align-items: center;
        font-size: 25rem;
      }
    }
  }
  > img {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
  }
}
</style>
