<template>
  <li
    class="home-item"
    @click="$router.push({ path: '/PlayMusic', query: { id: item.id, count: count } })">
    <img
      width="100%"
      :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=250x0`"
      alt=""
    />
    <h3>{{ item.name }}</h3>
    <div class="home-num">
      <img src="../assets/music.svg" alt="" />
      <div class="">{{ count }}</div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
    nowSong: Object,
  },
  data: function () {
    return {
      count:
        this.item.playCount > 100000000
          ? (this.item.playCount / 100000000).toFixed(2) + ""
          : this.item.playCount > 10000
          ? (this.item.playCount / 10000).toFixed(2) + ""
          : this.item.playCount,
    };
  },
};
</script>

<style lang="less" scoped>
.home-item {
  width: 32.5%;
  margin-bottom: 10rem;
  position: relative;
  h3 {
    font-size: 13rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    margin: 5rem;
  }
  .home-num {
    top: 0;
    right: 0;
    position: absolute;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: end;
    background-image: linear-gradient(270deg, #00000038,34%, transparent);
    img {
      height: 12rem;
      margin-right: 3rem;
    }
    div {
      font-size: 12rem;
      color: rgb(255, 255, 255);
      text-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.589);
    }
  }
}
</style>
