<template>
  <div>
    <!-- musicPlayBar -->
    <div v-if="song">
      <div
        @click="
          $emit('open-Page');
          overBody();
        "
        class="songBox"
        :class="playMusic ? 'musicPlay' : ''"
      >
        <div class="songImgBox">
          <img
            :src="`${
              song?.picUrl || song?.al.picUrl
            }?imageView=1&type=webp&thumbnail=150x0`"
            alt=""
          />
        </div>
        <div class="songCont">
          {{ song?.name }} -
          <span>{{ artistAll(song?.song?.album?.artists ?? song?.ar) }}</span>
        </div>
        <div class="songPlay">
          <canvas ref="canvas" width="300" height="300"></canvas>
          <div class="play" v-if="!playMusic" @click.stop="$emit('Update-play')">
            <img src="../assets/songPlay-bai-48.svg" alt="" />
          </div>
          <div class="stop" v-else @click.stop="$emit('Update-stop')">
            <img src="../assets/songPause-bai-48.svg" alt="" />
          </div>
        </div>

        <div class="songList" @click.stop="$emit('show-List')">
          <img src="../assets/songList-bai-48.svg" alt="" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playMusic: Boolean,
    updateTime: [Number, String],
    duration: [Number, String],
  },
  computed: {
    persontage: function () {
      return this.updateTime / this.duration;
    },
  },
  methods: {
    artistAll: function (arr) {
      let arrName = [];
      for (let i = 0; i < arr.length; i++) {
        arrName.push(arr[i].name);
      }
      return arrName.join("/");
    },
    overBody: function () {
      document.querySelector("body").style.overflow = "hidden";
    },
  },
  watch: {
    persontage: function (n) {
      // console.log(n);

      var canvas = this.$refs.canvas;
      var context = canvas.getContext("2d");
      
      context.strokeStyle = "#fff";
      context.clearRect(0, 0, 500, 500);

      context.beginPath();
      context.lineWidth=10;
      
      context.arc(150, 150, 120, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
      // context.closePath();

      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
.songBox {
  position: fixed;
  bottom: -1rem;
  transition: all 2s;
  align-items: center;
  display: flex;
  z-index: 200;
  width: 100%;
  padding: 2rem 10rem;
  background-color: #49403b;
  box-shadow: 0 1rem 1rem 0 #fff inset;
  &.musicPlay {
    .songImgBox {
      animation: songImgRotute 5s linear infinite;
      animation-play-state: running;
    }
  }
  .songImgBox {
    animation: songImgRotute 5s linear infinite;
    animation-play-state: paused;
    width: 50rem;
    flex-shrink: 0;
    position: relative;
    top: -5rem;
    transform-origin: 25rem 25rem;

    img {
      background-color: #000;
      border-radius: 50%;
      border: 7rem solid #000;
      width: 50rem;
      height: 50rem;
    }
    @keyframes songImgRotute {
      from {
        transform: rotate(0);
      }
      to {
        transform: rotate(360deg);
      }
    }
  }
  .songCont {
    width: 100%;
    padding: 10rem;
    color: #fff;
  }
  .songPlay {
    position: relative;
    width: 30rem;
    height: 30rem;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    canvas {
      width: 30rem;
      height: 30rem;
      position: absolute;
      top: calc(50% - 1.5rem);
      left: 50%;
      transform: translate(-50%, -50%) scale(1.3);
      z-index: -1;
    }
    div {
      width: 30rem;
      flex-shrink: 0;
      text-align: center;
      color: #fff;
      svg {
        width: 100%;
        height: 100%;
      }
    }
  }
  .songList {
    width: 30rem;
    margin-left: 10rem;
    flex-shrink: 0;
    text-align: center;
    img {
      width: 100%;
    }
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 1s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
