import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";
import IndexView from "../views/IndexView.vue";
import search from "../views/SearchView.vue";
import PlayMusic from "../views/PlayMusicView.vue";
import AlbumBestMatchView from "../views/AlbumBestMatchView.vue";
import ArtistBestMatchView from "../views/ArtistBestMatchView.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: IndexView,
    children: [
      { path: "/", component: HomeView },
      { path: "/hot", component: () => import(/* webpackChunkName: "about" */ "../views/HotView.vue") },
      { path: "/search", component: search },
    ],
  },
  {
    path: "/Artist",
    component: ArtistBestMatchView,
  },
  {
    path: "/Album",
    component: AlbumBestMatchView,
  },
  {
    path: "/PlayMusic",
    component: PlayMusic,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
