<template>
  <li class="home-song" @click="playMusic(item)">
    <div class="songLeft">
      <h3 :class="item.id === nowSong?.id ? 'active' : ''">{{ item.name }}</h3>
      <div class="author">
        <div v-if="item.song.copyright == 0" class="jl-sq"></div>
        <span>{{ artistAll(item.song.artists) }}-{{ item.song.album.name }}</span>
      </div>
    </div>
    <div class="play" v-if="item.id === nowSong?.id">
      <div class="playing" v-for="(key, index) in 4" :key="index"></div>
    </div>
    <div class="jl-play" v-else></div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
    nowSong: [Object],
  },
  methods: {
    playMusic: function (res) {
      this.$emit("Updatd-song", res);
    },
    artistAll: function (arr) {
      let arrName = [];
      for (let i = 0; i < arr.length; i++) {
        arrName.push(arr[i].name);
      }
      return arrName.join("/");
    },
  },
};
</script>

<style lang="less" scoped>
.flex() {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.home-song {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0 5rem 0;
  // margin: 10rem 0;
  margin: 10rem 5rem 10rem 7rem;
  border-bottom: 1rem solid #00000024;
  .songLeft {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    h3 {
      width: calc(100% - 25rem);
      font-size: 17rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      &.active {
        color: #f00;
      }
    }
    .author {
      margin-top: 5rem;
      display: flex;
      align-items: center;

      .jl-sq {
        margin-right: 5rem;
        width: 15rem;
        height: 11rem;
        background-image: url(../assets/jingling.png);
        background-size: 180rem;
        background-position: 1rem 2rem;
      }
      span {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 12px;
    color: #888;
      }
    }
  }
  .play {
    .flex();
    justify-content: space-around;
    width: 30rem;
    height: 30rem;
    padding: 3.5rem 3.5rem 3.5rem 0;
    @keyframes informationMove {
      0% {
        transform: rotateX(60deg);
      }
      50% {
        transform: rotateX(30deg);
      }
      100% {
        transform: rotateX(60deg);
      }
    }
    .playing {
      width: 10%;
      height: 100%;
      background-color: #f00;
      border-radius: 50rem;
      transform-origin: bottom;
      animation: informationMove 0.8s ease-in-out infinite;
      &:nth-child(1) {
        animation-delay: -0.1s;
      }
      &:nth-child(2) {
        animation-delay: -0.2s;
      }
      &:nth-child(3) {
        animation-delay: -0.3s;
      }
      &:nth-child(4) {
        animation-delay: -0.4s;
      }
    }
  }
  .jl-play {
    width: 30rem;
    height: 30rem;
    background-image: url(../assets/jingling.png);
    background-size: 179rem;
    background-position: 333rem 0rem;
  }
}
</style>
